body {
  color: #27253d;
  background: #e6f3f8;
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: 1.1rem;
  line-height: 1.7;
}

main {
  min-height: calc(100vh - 4rem);
  padding: 1rem 1rem 3rem;
  position: relative;
}

h1 {
  margin-top: 0;
}

.hidden {
  display: none;
}

.pagination-container {
  width: calc(100% - 2rem);
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
  display: flex;
  position: absolute;
  bottom: 0;
}

.pagination-number, .pagination-button {
  cursor: pointer;
  height: 2.5rem;
  width: 2.5rem;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: .2rem;
  margin: .25rem;
  font-size: 1.1rem;
}

.pagination-number:hover, .pagination-button:not(.disabled):hover {
  background: #fff;
}

.pagination-number.active {
  color: #fff;
  background: #0085b6;
}

